<script>
  import echarts from 'echarts'
  import world from '@/../static/common/js/world.js'
  export default {
    data() {
      return {
        tag: [
          {name: 'javaScript'},
          {name: 'jQury'},
          {name: 'HTML(5)'},
          {name: 'CSS(3)'},
          {name: 'NodeJs'},
          {name: 'Bootstrap'},
          {name: 'Vue'},
          {name: 'wxapp'}
        ],
        skills: [
          {name: 'HTML(5)', spot: 99},
          {name: 'CSS(3)', spot: 95},
          {name: 'javaScript', spot: 96},
          {name: 'jQuery', spot: 95},
          {name: 'Vue', spot: 86},
          {name: 'WXApp', spot: 85},
          {name: 'Node', spot: 80},
          {name: 'Bootstrap', spot: 80},
          {name: 'php', spot: 65},
          {name: 'Less', spot: 50},
          {name: 'Sass', spot: 45},
          {name: 'Gulp', spot: 44},
          {name: 'Grunt', spot: 40},
          {name: 'webpack', spot: 40},
          {name: 'Angular', spot: 25}
        ],
        showSpot: false
      }
    },
    computed: {
    },
    created () {

    },
    methods: {
      // tag
      tagBgColor() {
        return this.$utils.randomColor()
      },
      // 
      bubble() {
        let width, height, canvas, ctx, circles
        let bubble = document.getElementById('bubble')

        function initHeader() {
          try {
            canvas = document.getElementById('bubble')
            if (bubble) {
              width = window.innerWidth * 0.25 > 300 ? window.innerWidth * 0.25 : 300
              // height = window.innerHeight
              height = 255
              canvas.width = width
              canvas.height = height
              ctx = canvas.getContext('2d')
              circles = []
              for (var x = 0; x < width * 0.5; x++) {
                var c = new Circle()
                circles.push(c)
              }
              animate()
            }
          } catch (ex) {
            if (window.cosole && window.cosole.log) {
              console.log(ex)
            }
          }
        }

        function animate() {
          ctx.clearRect(0, 0, width, height)
          for (var i in circles) {
            circles[i].draw()
          }
          window.requestAFrame = (function () {
            return window.requestAnimationFrame || window.webkitRequestAnimationFrame || window.mozRequestAnimationFrame || window.oRequestAnimationFrame || window.msRequestAnimationFrame || function (callback) { return window.setTimeout(callback, 1000 / 60) }
          })()
          /* eslint-disable */
          requestAFrame(animate)
          /* eslint-enable */
        }

        // Canvas manipulation
        function Circle() {
          var _this = this;

          // constructor
          (function() {
            _this.pos = {}
            init()
          })()

          function init() {
            _this.pos.x = Math.random() * width
            _this.pos.y = height + Math.random() * 100
            _this.alpha = 0.1 + Math.random() * 0.3
            _this.scale = 0.1 + Math.random() * 0.3
            _this.velocity = Math.random()
          }

          this.draw = function() {
            if (_this.alpha <= 0) {
              init()
            }
            _this.pos.y -= _this.velocity
            _this.alpha -= 0.0005
            ctx.beginPath()
            ctx.arc(_this.pos.x, _this.pos.y, _this.scale * 10, 0, 2 * Math.PI, false)
            ctx.fillStyle = 'rgba(255,255,255,' + _this.alpha + ')'
            ctx.fill()
          }
        }

        initHeader()
      },
      // 
      indexInit() {
        // get skill
        // 
        let that = this
        let skillV = []
        let skill = []
        for (let i = 0; i < that.$data.skills.length; i++) {
          skill.push({text: that.$data.skills[i].name, max: 100})
          skillV.push(that.$data.skills[i].spot)
        }
        // domecharts
        let myChart = echarts.init(document.getElementById('skill'))
        let trigger = 'item'
        console.log('----------------')
        console.log(document.body.clientWidth)
        console.log('------ End------')
        if (document.body.clientWidth < 768) {
          trigger = 'none'
        }
        // 
        let option = {
          title: {
            text: '',
            x: 'center'
          },
          tooltip: {
            trigger: trigger
          },
          color: ['#19a2de'],
          radar: [
            {
              indicator: skill,
              conter: ['50%', '50%'],
              radius: 100
            }
          ],
          series: [
            {
              type: 'radar',
              tooltip: {
                trigger: trigger
              },
              itemStyle: {normal: {areaStyle: {type: 'default'}}},
              data: []
            }
          ]
        }
        //  
        myChart.setOption(option)
        // 
        window.onscroll = function() {
          let skill = document.querySelector('.skill')
          if (!skill) {
            return false
          }
          let scrollTop
          if (document.documentElement && document.documentElement.scrollTop) {
            scrollTop = document.documentElement.scrollTop
          } else if (document.body) {
            scrollTop = document.body.scrollTop
          }
          let scrollTopValue = scrollTop
          let skillHeight = skill.scrollHeight
          let skillTopHeight = skill.offsetTop
          // .skill

          if ((scrollTopValue - skillTopHeight) >= -20 && (scrollTopValue - skillTopHeight) <= skillHeight / 2 + 380) {
            that.$data.showSpot = true
            // 
            //  setTimeout 
            setTimeout(function () {
              myChart.setOption({
                series: [
                  {
                    data: [{
                      value: skillV,
                      name: ''
                    }]
                  }
                ]
              })
            }, 500)
          }
        }
        // Skill End
      },
      showWxImg() {

      },
      initMap () {
        // eslint-disable-next-line new-cap
        let _map = new world('world')

        let mapData = [
          [110, 19, ''],
          [106, 23, ''],
          [105, 24, ''],
          [105, 25, ''],
          [104, 26, ''],
          [104, 28, ''],
          [107, 21, ''],
          [108, 22, ''],
          [110, 21, ''],
          [108, 25, '']
        ]
        mapData.forEach(d => {
          _map.add([d[0], d[1]], {
            title: d[2]
          })
        })

        return () => {
          _map.destory()
        }
      }
    },
    mounted () {
      this.$store.dispatch('setSiteTitle', 'Falost  - ')
      this.bubble()
      this.indexInit()
      this.initMap()
    }
  }
</script>
